.movies {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}

.movies .movie {
    width: 40%;
    height: 50%;
    margin: 40px;
    border-bottom: 5px solid rgba(0,0,0,0.05);
    display: flex;
    padding-bottom: 50px;
    background-color: azure;
    box-shadow: 10px 20px 50px rgb(134, 130, 130);
    border-radius: 10%;
    position: relative;
}



.movie img {
    position: absolute;
    top: -50px;
    left: 10px;
    border-radius: 10px;
    max-width: 200px;
    max-height: 300px;
}
.movie-data {
    padding-left: 35%;
}
.movie-data h2 a {
    text-decoration: none;
    color: black;
}

.movie-data p {
    font-size: 12px;
    color: rgb(69, 65, 69);
}
.movie ul {
    margin-left: -30px;
}
.movie ul h3 {
    font-size: 14px;
    opacity: 0.9;
    font-weight: 300;
}
.movie ul li {
    list-style: none;
    font-size: 7px;
    color: gray;
}
